// Popular-77/frontend/src/pages/Home.tsx ( HomePage.tsx)
import React, { useRef } from 'react';
import Hero from '../components/Hero';               // Hero  
import PopularTools from '../components/PopularTools'; //    
import ToolsPreview from '../components/ToolsPreview'; //     
import PageTitle from '../shared/PageTitle';

//  constants.ts TOOLS  
import { TOOLS } from '../data/constants';

const Home: React.FC = () => {
  //    ref .
  const allToolsRef = useRef<HTMLDivElement>(null);
  const pdfToolsRef = useRef<HTMLDivElement>(null);
  const imageToolsRef = useRef<HTMLDivElement>(null);
  const aiToolsRef = useRef<HTMLDivElement>(null);
  const imageConvertRef = useRef<HTMLDivElement>(null);
  const documentToolsRef = useRef<HTMLDivElement>(null);
  const youtubeToolsRef = useRef<HTMLDivElement>(null);
  const videoToolsRef = useRef<HTMLDivElement>(null);

  //     
  const scrollToSection = (ref: React.RefObject<HTMLDivElement>) => {
    ref.current?.scrollIntoView({ behavior: 'smooth' });
  };

  //  featured: true       
  const featuredTools = TOOLS.filter(tool => tool.featured);

  return (
    //  Navbar Footer App.tsx    .
    //    main  ,    App.tsx main 
    //          .
    <>
      <PageTitle />
      <main>
      {/* 1.   */}
      <Hero />

      {/* 2.    */}
      {/*  featuredTools prop  */}
      <PopularTools 
        tools={featuredTools} 
        refs={{
          allToolsRef,
          pdfToolsRef,
          imageToolsRef,
          aiToolsRef,
          imageConvertRef,
          documentToolsRef,
          youtubeToolsRef,
          videoToolsRef
        }}
        scrollToSection={scrollToSection}
      />

      {/* 3.    (  ) */}
      {/*  TOOLS  prop  */}
      <ToolsPreview tools={TOOLS} />

      {/* TODO:       . */}
      {/* :  ,       */}
      </main>
    </>
  );
};

export default Home;
