import { ArrowRight, Grid3X3, FileText, Image, Brain, RefreshCw, FileType, Youtube, Video } from 'lucide-react';
import { Tool } from '../types';

interface PopularToolsProps {
  tools: Tool[];
  refs?: {
    allToolsRef: React.RefObject<HTMLDivElement>;
    pdfToolsRef: React.RefObject<HTMLDivElement>;
    imageToolsRef: React.RefObject<HTMLDivElement>;
    aiToolsRef: React.RefObject<HTMLDivElement>;
    imageConvertRef: React.RefObject<HTMLDivElement>;
    documentToolsRef: React.RefObject<HTMLDivElement>;
    youtubeToolsRef: React.RefObject<HTMLDivElement>;
    videoToolsRef: React.RefObject<HTMLDivElement>;
  };
  scrollToSection?: (ref: React.RefObject<HTMLDivElement>) => void;
}

const PopularTools: React.FC<PopularToolsProps> = ({ tools, refs, scrollToSection }) => {
  const categories = [
    { name: ' ', targetId: 'all-tools', icon: Grid3X3, ref: refs?.allToolsRef },
    { name: 'PDF', targetId: 'pdf-tools', icon: FileText, ref: refs?.pdfToolsRef },
    { name: '', targetId: 'image-tools', icon: Image, ref: refs?.imageToolsRef },
    { name: 'AI', featured: true, targetId: 'ai-tools', icon: Brain, ref: refs?.aiToolsRef },
    { name: '', targetId: 'image-conversion-tools', icon: RefreshCw, ref: refs?.imageConvertRef },
    { name: '', targetId: 'document-tools', icon: FileType, ref: refs?.documentToolsRef },
    { name: 'YOUTUBE', targetId: 'youtube-tools', icon: Youtube, ref: refs?.youtubeToolsRef },
    { name: '', targetId: 'video-tools', icon: Video, ref: refs?.videoToolsRef },
  ];

  const scrollToSectionFallback = (targetId: string) => {
    const element = document.getElementById(targetId);
    if (element) {
      const elementPosition = element.offsetTop;
      const offsetPosition = elementPosition - 200; // 200px   h3      
      
      window.scrollTo({
        top: offsetPosition,
        behavior: 'smooth'
      });
    }
  };

  const handleCategoryClick = (category: any) => {
    if (scrollToSection && category.ref) {
      scrollToSection(category.ref);
    } else {
      scrollToSectionFallback(category.targetId);
    }
  };

  return (
    <section className="py-20 bg-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="text-center mb-12">
          <h2 className="text-3xl font-bold text-gray-900"> </h2>
          <p className="mt-4 text-xl text-gray-600 font-bold">      .</p>
        </div>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          {categories.map((category) => {
            const IconComponent = category.icon;
            return (
              <div
                key={category.name}
                onClick={() => handleCategoryClick(category)}
                className="px-6 py-4 rounded-lg font-semibold text-center transition-all duration-200 bg-blue-100 text-blue-800 hover:bg-blue-200 hover:shadow-md cursor-pointer flex flex-col items-center gap-2"
              >
                <IconComponent className="w-6 h-6" />
                <span className="text-sm">{category.name}</span>
              </div>
            );
          })}
        </div>
        <div className="text-center mt-12">
          <div 
            onClick={() => window.open('https://popular-77-deoe.vercel.app/tools/pdf-doc', '_blank')}
            className="text-blue-600 hover:text-blue-800 font-semibold inline-flex items-center text-xl cursor-pointer"
          >
            77       .
            <ArrowRight className="ml-2 w-4 h-4" />
          </div>
        </div>
      </div>

      {/*    */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 mt-16">
        {/*    */}
        <div ref={refs?.allToolsRef} id="all-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
             
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-1 gap-6">
            <div className="bg-gray-50 p-6 rounded-lg hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">  </h4>
              <p className="text-gray-600">     .</p>
            </div>
          </div>
        </div>

        {/* PDF  */}
        <div ref={refs?.pdfToolsRef} id="pdf-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
            PDF
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            <div 
              onClick={() => window.open('https://popular-77-deoe.vercel.app/tools/pdf-doc', '_blank')}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to DOCX</h4>
              <p className="text-gray-600">PDF  DOCX .</p>
            </div>
            <div className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">PDF </h4>
              <p className="text-gray-600"> PDF   .</p>
            </div>
            <div className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">PDF </h4>
              <p className="text-gray-600">PDF    .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-jpg'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to JPG</h4>
              <p className="text-gray-600">PDF  JPG  .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-png'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to PNG</h4>
              <p className="text-gray-600">PDF  PNG  .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-xls'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to Excel</h4>
              <p className="text-gray-600">PDF  Excel  .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-pptx'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to PPTX</h4>
              <p className="text-gray-600">PDF  PPTX .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-svg'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to SVG</h4>
              <p className="text-gray-600">PDF  SVG   .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-tiff'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to TIFF</h4>
              <p className="text-gray-600">PDF  TIFF  .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-bmp'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to BMP</h4>
              <p className="text-gray-600">PDF  BMP  .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-gif'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to GIF</h4>
              <p className="text-gray-600">PDF  GIF  .</p>
            </div>
            <div 
              onClick={() => window.location.href = '/tools/pdf-ai'}
              className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 cursor-pointer transform hover:scale-105"
            >
              <h4 className="font-semibold text-lg mb-2">PDF to AI() </h4>
              <p className="text-gray-600">PDF  AI()  .</p>
            </div>
          </div>
        </div>

        {/*   */}
        <div ref={refs?.imageToolsRef} id="image-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
            
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-green-50 p-6 rounded-lg hover:bg-green-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">   .</p>
            </div>
            <div className="bg-green-50 p-6 rounded-lg hover:bg-green-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">    .</p>
            </div>
            <div className="bg-green-50 p-6 rounded-lg hover:bg-green-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">     .</p>
            </div>
          </div>
        </div>

        {/* AI  */}
        <div ref={refs?.aiToolsRef} id="ai-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
            AI
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-purple-50 p-6 rounded-lg hover:bg-purple-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">AI  </h4>
              <p className="text-gray-600">    .</p>
            </div>
            <div className="bg-purple-50 p-6 rounded-lg hover:bg-purple-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">AI  </h4>
              <p className="text-gray-600">   .</p>
            </div>
            <div className="bg-purple-50 p-6 rounded-lg hover:bg-purple-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">AI </h4>
              <p className="text-gray-600"> AI   .</p>
            </div>
          </div>
        </div>

        {/*   */}
        <div ref={refs?.imageConvertRef} id="image-conversion-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
            
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            <div className="bg-indigo-50 p-6 rounded-lg hover:bg-indigo-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">JPG </h4>
              <p className="text-gray-600">  JPG .</p>
            </div>
            <div className="bg-indigo-50 p-6 rounded-lg hover:bg-indigo-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">PNG </h4>
              <p className="text-gray-600"> PNG  .</p>
            </div>
            <div className="bg-indigo-50 p-6 rounded-lg hover:bg-indigo-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">WebP </h4>
              <p className="text-gray-600"> WebP  .</p>
            </div>
            <div className="bg-indigo-50 p-6 rounded-lg hover:bg-indigo-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2">GIF </h4>
              <p className="text-gray-600"> GIF .</p>
            </div>
          </div>
        </div>

        {/*   */}
        <div ref={refs?.documentToolsRef} id="document-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
            
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-orange-50 p-6 rounded-lg hover:bg-orange-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">    .</p>
            </div>
            <div className="bg-orange-50 p-6 rounded-lg hover:bg-orange-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">   .</p>
            </div>
            <div className="bg-orange-50 p-6 rounded-lg hover:bg-orange-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600"> PPT .</p>
            </div>
          </div>
        </div>

        {/* YOUTUBE  */}
        <div ref={refs?.youtubeToolsRef} id="youtube-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
            YOUTUBE
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-red-50 p-6 rounded-lg hover:bg-red-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">  .</p>
            </div>
            <div className="bg-red-50 p-6 rounded-lg hover:bg-red-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">   .</p>
            </div>
            <div className="bg-red-50 p-6 rounded-lg hover:bg-red-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">   .</p>
            </div>
          </div>
        </div>

        {/*   */}
        <div ref={refs?.videoToolsRef} id="video-tools" className="mb-16 scroll-mt-20">
          <h3 className="text-2xl font-bold text-gray-900 mb-6">
            
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">    .</p>
            </div>
            <div className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">     .</p>
            </div>
            <div className="bg-blue-50 p-6 rounded-lg hover:bg-blue-100 hover:shadow-lg transition-all duration-200 transform hover:scale-105">
              <h4 className="font-semibold text-lg mb-2"> </h4>
              <p className="text-gray-600">   .</p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default PopularTools;